        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    text-align: center;
    height: 100%;
    width: 500px;
    background: rgba(0, 0, 0, 0.7);
  }
  .admin_signin > div{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
  }
  .admin_signin input{
    background: none;
  }
  .admin_signin input::-webkit-input-placeholder{
    color: white;
  }
  .admin_signin input:-ms-input-placeholder{
    color: white;
  }
  .admin_signin input::-ms-input-placeholder{
    color: white;
  }
  .admin_signin input::placeholder{
    color: white;
  }
  .admin_signin button{
    font-family: "Heebo", sans-serif;
    padding: 10px;
    border: none;
    margin-top: 15px;
    height: 60px;
    width: 200px;
    font-weight: 900;
    color: white;
    font-size: 18px;
    outline: 2px solid white;
    outline-offset: -5px;
    background-position: center;
    background-size: cover;
    background: none;
    background-color: rgba(255,255,255,0);
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
  }
  .admin_signin button:hover{
    outline-offset: 0px;
    cursor: pointer;
  }

  .group 			  {
    position:relative;
    margin-bottom:45px;
  }
  input 				{
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width:300px;
    border:none;
    border-bottom:1px solid #757575;
    color: white;
  }
  input:focus 		{ outline:none; }

  /* LABEL ======================================= */
  label 				 {
    color:#999;
    font-size:18px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    -o-transition:0.2s ease all;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
  }

  /* active state */
  input:focus ~ label, input:valid ~ label 		{
    top:-20px;
    font-size:14px;
    color:rgb(255, 255, 255);
  }

  /* BOTTOM BARS ================================= */
  .bar 	{ position:relative; display:block; width:300px; }
  .bar:before, .bar:after 	{
    content:'';
    height:2px;
    width:0;
    bottom:1px;
    position:absolute;
    background:rgb(255, 255, 255);
    -o-transition:0.2s ease all;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
  }
  .bar:before {
    left:50%;
  }
  .bar:after {
    right:50%;
  }

  /* active state */
  input:focus ~ .bar:before, input:focus ~ .bar:after {
    width:50%;
  }

  /* HIGHLIGHTER ================================== */
  .highlight {
    position:absolute;
    height:60%;
    width:100px;
    top:25%;
    left:0;
    pointer-events:none;
    opacity:0.5;
  }

  /* active state */
  input:focus ~ .highlight {
    -webkit-animation:inputHighlighter 0.3s ease;
    animation:inputHighlighter 0.3s ease;
  }

  /* ANIMATIONS ================ */
  @-webkit-keyframes inputHighlighter {
    from { background:rgb(255, 255, 255); }
    to 	{ width:0; background:transparent; }
  }
  @keyframes inputHighlighter {
    from { background:rgb(255, 255, 255); }
    to 	{ width:0; background:transparent; }
  }


